<template>
    <div class="container">
        <p>Hasil pencarian Anda : <strong>{{ $route.query.txt }}</strong></p>

        <form class="mb-3">
            <div class="form-row">
                <div class="col-3">
                    <select class="form-control" v-model="channel" @change="getKategori($event.target.value)">
                        <option value="">Pilih Channel</option>
                        <option v-for="(i, idx) in listChannel" :key="idx">{{ i }}</option>
                    </select>
                </div>
                <div class="col-3">
                    <select class="form-control" v-model="kategori"  @change="berita()" :disabled="channel == ''">
                        <option value="">Pilih Kategori</option>
                        <option v-for="(i, idx) in listKategori" :key="idx">{{ i }}</option>
                    </select>
                </div>
            </div>
        </form>
        <div v-if="isLoading">Loading...</div>
        <div v-else>
            <div class="row">
                <div class="col" v-if="listBerita.length == 0">Tidak ada data</div>
            </div>
            <div class="row">
                <div class="col-md-3 mb-3" v-for="(berita, index) in listBerita" :key="index">
                    <CardVue :berita="berita" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import CardVue from '../components/Berita.vue';
// import CardSkelekton from '../components/CardSkelekton.vue';

export default {
    name: 'SearchView',
    components: {
        CardVue,
        // CardSkelekton
    },
    data(){
        return {
            listBerita: [],
            listChannel: ['antara','cnbc','cnn','jpnn','kumparan','merdeka','okezone','republika','sindonews','suara','tempo','tribun'],
            listKategori: [],
            channel: "cnn",
            kategori: "terbaru",
            isLoading: false
        }
    },
    created(){
        console.log("created at ")
        this.berita()
    },
    mounted(){
        const searchTerm = this.$route?.query?.txt;
        if (searchTerm) {
            console.log('Search term from URL on mounted:', this.$route.query.txt);
            this.berita();
        }
    },
    watch: {
        '$route' () {
            const searchTerm = this.$route?.query?.txt;
            if (searchTerm) {
                console.log('Search term from URL on watch:', this.$route.query.txt);
                this.berita();
            }
        }
    },
    methods: {
        async berita(){
            this.isLoading = true
            const cachedData = JSON.parse(localStorage.getItem('cachedData'));
            if(cachedData){
                this.isLoading = false
                this.listBerita = cachedData
            } else {
                localStorage.removeItem('cachedData')
                try {
                    const res = await this.$axios.get(`/${this.channel}/${this.kategori}`)
                    console.log(res.data.data.posts)
                    if(res.data.success){
                        this.listBerita = res.data.data.posts
                        localStorage.setItem('cachedData', JSON.stringify(res.data.data.posts));
                    } else {

                        this.listBerita = []
                    }
                    
                } catch (error) {
                    console.log(error)
                } finally {
                    this.isLoading = false
                }
            }

            
            
        },
        async getKategori(primary){
            // console.log(primary)
            this.listKategori = []
            this.kategori = ""

            let data = [
                {
                    primary: 'antara',
                    paths: [
                    'terbaru',
                    'politik',
                    'hukum',
                    'ekonomi',
                    'bola',
                    'olahraga',
                    'humaniora',
                    'lifestyle',
                    'hiburan',
                    'dunia',
                    'tekno',
                    'otomotif',
                    ],
                },
                {
                    primary: 'cnbc',
                    paths: [
                    'terbaru',
                    'investment',
                    'news',
                    'market',
                    'entrepreneur',
                    'syariah',
                    'tech',
                    'lifestyle',
                    'opini',
                    'profil',
                    ],
                },
                {
                    primary: 'cnn',
                    paths: [
                    'terbaru',
                    'nasional',
                    'internasional',
                    'ekonomi',
                    'olahraga',
                    'teknologi',
                    'hiburan',
                    'gayaHidup',
                    ],
                },
                {
                    primary: 'jpnn',
                    paths: ['terbaru'],
                },
                {
                    primary: 'kumparan',
                    paths: ['terbaru'],
                },
                {
                    primary: 'merdeka',
                    paths: [
                    'terbaru',
                    'jakarta',
                    'dunia',
                    'gaya',
                    'olahraga',
                    'teknologi',
                    'otomotif',
                    'khas',
                    'sehat',
                    'jateng',
                    ],
                },
                {
                    primary: 'okezone',
                    paths: [
                    'terbaru',
                    'celebrity',
                    'sports',
                    'otomotif',
                    'economy',
                    'techno',
                    'lifestyle',
                    'bola',
                    ],
                },
                {
                    primary: 'republika',
                    paths: [
                    'terbaru',
                    'news',
                    'daerah',
                    'khazanah',
                    'islam',
                    'internasional',
                    'bola',
                    'leisure',
                    ],
                },
                {
                    primary: 'sindonews',
                    paths: [
                    'terbaru',
                    'nasional',
                    'metro',
                    'ekbis',
                    'international',
                    'daerah',
                    'sports',
                    'otomotif',
                    'tekno',
                    'sains',
                    'edukasi',
                    'lifestyle',
                    'kalam',
                    ],
                },
                {
                    primary: 'suara',
                    paths: [
                    'terbaru',
                    'bisnis',
                    'bola',
                    'lifestyle',
                    'entertainment',
                    'otomotif',
                    'tekno',
                    'health',
                    ],
                },
                {
                    primary: 'tempo',
                    paths: [
                    'nasional',
                    'bisnis',
                    'metro',
                    'dunia',
                    'bola',
                    'cantik',
                    'tekno',
                    'otomotif',
                    'seleb',
                    'gaya',
                    'travel',
                    'difabel',
                    'creativelab',
                    'inforial',
                    'event',
                    ],
                },
                {
                    primary: 'tribun',
                    paths: [
                    'terbaru',
                    'bisnis',
                    'superskor',
                    'sport',
                    'seleb',
                    'lifestyle',
                    'travel',
                    'parapuan',
                    'otomotif',
                    'techno',
                    'kesehatan',
                    ],
                },
            ]

            if(primary != ""){
                let response = data.filter(item => item.primary == primary)
                let kategories = []

                response[0].paths.map(i => {
                    kategories.push(i)
                })

                this.listKategori = kategories;
            } else {
                this.listKategori = []
            }

            // console.log(this.listKategori)
        }
    }
}

</script>