<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <div class="container">
      <router-link class="navbar-brand" to="/">Vue App</router-link>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li class="nav-item">
            <router-link class="nav-link" to="/">Home</router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/about">About</router-link>
          </li>
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" role="button" data-toggle="dropdown" aria-expanded="false">
              Profil
            </a>
            <div class="dropdown-menu">
              <router-link class="dropdown-item" to="/profil">Sejarah</router-link>
              <router-link class="dropdown-item" to="/visi-misi">Visi Misi</router-link>
              <div class="dropdown-divider"></div>
              <router-link class="dropdown-item" to="/struktur-organisasi">Struktur Organisasi</router-link>
            </div>
          </li>
        </ul>
        <form class="form-inline my-2 my-lg-0" @submit.prevent="search">
          <input class="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" v-model="searchQuery">
          <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
        </form>
      </div>
    </div>
  </nav>

  <transition  name="fade">
    <router-view></router-view>
  </transition>


  <button class="btn btn-primary" id="buttonTo" @click="scrollToTop" v-if="showButtonToTop">Scroll to top</button>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  data() {
    return {
      searchQuery: '',
      showButtonToTop: false
    }
  },
  watch: {
    '$route' (to, from) {
      document.title = to.meta.title || 'Vue App'
    }
  },
  mounted(){
      window.addEventListener("scroll", this.handleScroll)
    },
  beforeUnmount(){
    window.removeEventListener("scroll", this.handleScroll)
  },
  methods: {
    search(){
      this.$router.push({
        name: 'SearchView',
        query: {
          txt: this.searchQuery
        }
      })
    },
    handleScroll(){
      console.log(window.scrollY, this.showButtonToTop)
      if(window.scrollY > 100){
        this.showButtonToTop = true
        console.log("muncul button ")
      } else {
        this.showButtonToTop = false
        console.log("button hilang")
      }
      
    },
    scrollToTop(){
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>

